body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.module {
  margin: 0 auto;
  height: 100vh;
  max-width: 1440px;
  display: flex;
  background-color: yellow;
}
@media screen and (max-width: 1000px) {
  .module {
    flex-direction: column;
    height: auto;
  }
}

.left_container {
  width: 51%;
}
@media screen and (max-width: 1000px) {
  .left_container {
    width: 95%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1440px) {
  .left_container {
    width: 735px;
  }
}

.right_container {
  width: 49%;
}
@media screen and (max-width: 1000px) {
  .right_container {
    width: 95%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1440px) {
  .right_container {
    width: 705px;
  }
}

img {
  display: block;
  margin-top: 96px;
  margin-left: auto;
  width: 505px;
  height: 330px;
  content: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg");
}
@media screen and (max-width: 1000px) {
  img {
    margin-top: 20px;
    width: 100%;
    height: auto;
    content: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  }
}
@media screen and (min-width: 1440px) {
  img {
    content: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg");
  }
}

h1 {
  margin-top: 136px;
  margin-bottom: 0;
  margin-left: 95px;
  font-size: 42px;
  font-weight: 700;
  line-height: 50.4px;
}
@media screen and (max-width: 1000px) {
  h1 {
    width: 95%;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
  }
}

p {
  margin-top: 24px;
  margin-bottom: 0;
  margin-left: 95px;
  width: 380px;
  height: 96px;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
}
@media screen and (max-width: 1000px) {
  p {
    width: 95%;
    height: auto;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
  }
}

button {
  margin-top: 32px;
  margin-left: 95px;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  padding: 16px 40px;
  border: none;
  background-color: #C4C1AE;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  cursor: pointer;
}
@media screen and (max-width: 1000px) {
  button {
    margin-left: calc(50% - 64px);
    margin-top: 20px;
    margin-bottom: 20px;
  }
}